import FieldType from '../model/FieldType';
import IField from '../model/IField';

const layouts: FieldType[] = [
    FieldType.Group,
    FieldType.Paper,
    FieldType.Expansion,
    FieldType.Div,
    FieldType.Fragment,
];

/**
 *    stateless,    
 *  
 */
export const isStatefull = ({type, name}: IField) => name && !layouts.includes(type);

export default isStatefull;
